.menu-screen {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(8px);
    /* translucent over game */
    background-color: rgba(0, 0, 0, 0.5);
    /* semi-transparent overlay */
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.menu-title {
    font-size: 4rem;
    color: white;
    margin-bottom: 2rem;
}

.menu-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.high-score {
    color: white;
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.start-button {
    padding: 0.7rem 1.5rem;
    font-size: 1.2rem;
    border: none;
    background: white;
    color: black;
    border-radius: 6px;
    cursor: pointer;
    transition: 0.3s;
}

.start-button:hover {
    background: #ddd;
}